function toEth(bn) {
  return Math.floor(Number(ethers.utils.formatEther(bn.toString())) * 100) / 100;
}

async function getDetailNFT(nftId) {
  const tokenURI = await warriorContract.tokenURI(nftId);
  const response = await axios.get(tokenURI);
  const image = response.data.image;
  const name = response.data.name;

  const warStat = await warContract.getNftStat(nftId);
  // nftId2connect[nftId],
  // nftId2turn[nftId],
  // nftId2power[nftId],
  // nftId2earning[nftId]

  const power = Number(warStat[2].toString());
  const claimedEarning = BN.from(warStat[3].toString());

  const warriorStat = await warriorContract.getStat(nftId);
  // (id, coll, genetic, createdTime, noFeeUnsummonTime, rarity);

  const collateral = BN.from(warriorStat[1].toString());
  const createdTime = Number(warriorStat[3].toString());
  const noFeeUnsummonTime = Number(warriorStat[4].toString());
  const rarity = Number(warriorStat[5].toString());

  let totalReward = await warContract.getTotalReward(nftId, 0);
  totalReward = BN.from(totalReward[0].toString());

  const age = moment().unix() - createdTime;
  const totalEarning = totalReward.add(claimedEarning);
  let totalEarningUsd = await mountainContract.tokenToCoin(totalEarning.toString());
  totalEarningUsd = BN.from(totalEarningUsd.toString());
  const pctg = totalEarningUsd.mul(10000).div(collateral);
  const pctgDaily = pctg.mul((3600 * 24)).div(age);
  const pctgYearly = pctg.mul((3600 * 24 * 365)).div(age);
  const ageDays = Math.floor((age / (3600 * 24)) * 100) / 100;
  const ret = {
    nftId: nftId,
    power: power,
    earning: toEth(totalReward),
    noPenaltyTime:
      noFeeUnsummonTime,
    rarity,
    collateral: toEth(collateral),
    image,
    name,
    age: ageDays,
    totalEarning: toEth(totalEarning),
    totalEarningUsd: toEth(totalEarningUsd),
    pctg: pctg.toNumber() / 100,
    pctgDaily: pctgDaily.toNumber() / 100,
    pctgYearly: pctgYearly.toNumber() / 100
  };

  return ret;
}


async function getNextSessionTime() {
  let stat = await warContract.getStat();
  let ts = Number(stat[4].toString());
  return ts;
}

async function retrieveReward(nftId) {
  const tx = await warContract.withdrawReward(nftId, 0).sendTransaction({ from: account }).executed();
  const txHash = tx.transactionHash;
  return txHash;
}

async function burn(nftId) {
  const tx = await warriorContract.unsummon(nftId).sendTransaction({ from: account }).executed();
  const txHash = tx.transactionHash;
  return txHash;
}


